import { useRef, useState, useEffect } from 'react';
import { Image, View, Text, StyleSheet, TouchableOpacity, Animated } from "react-native";
import Svg from "react-native-svg";
import { Back } from "../../Components";
import { FISHES } from './constants';
import { Fish } from '../../Components';

const AnimatedSvg = Animated.createAnimatedComponent(Svg);

export default function Inventory(props) {
  const { onClickMenu, onSelectFish, setFishes, fish: selected, fishes, bank, adjustBank } = props;
  const [ fish, setFish ] = useState(selected);
  const translation = useRef(new Animated.Value(0)).current;
  const rotation = useRef(new Animated.Value(0)).current;
  const [reverse, setReverse] = useState(false);
  const handleUnlock = () => {
    if(fish.price <= bank){
      adjustBank(bank - fish.price);
      setFishes([...new Set([...fishes, fish.id])]);
    }
  };
  useEffect(() => {
      Animated.timing(translation, {
        toValue: !reverse ? 30 : 0,
        useNativeDriver: true,
        duration: 3000,
      }).start(() => setReverse(!reverse));
  }, [reverse]);
  useEffect(() => {
      Animated.timing(rotation, {
        toValue: !reverse ? 30 : 0,
        useNativeDriver: true,
        duration: 1000,
      }).start(() => setReverse(!reverse));
  }, [reverse]);
  const rotate = rotation.interpolate({
      inputRange: [0, 30],
      outputRange: ['-5deg', '5deg'],
  });
  const { id } = fish;
  return (
    <>
      <View style={styles.container}>
        <Image style={[StyleSheet.absoluteFill, styles.background]} source={require("../../assets/bg.png")} />
        <View style={styles.layout}>
          <View style={styles.preview}>
            <Text style={styles.name}>{fish.name}</Text>
            <View style={styles.previewImage}>
              <AnimatedSvg
                viewBox="0 0 64 64"
                style={[
                  styles.activeFish, 
                  { transform: [{ translateY: translation }, { rotateZ: rotate }] }
                ]}
              >
                <Fish fish={fish} posX={32} posY={32} /> 
              </AnimatedSvg>
              {!fishes.includes(fish.id) && <>
                <Image style={styles.biglock} source={require('../../assets/lock.png')} />
                <View style={styles.info}>
                  <Text style={styles.about}>Price: {fish.price} Bubbles</Text>
                </View>
              </>}
              {selected.id === fish.id && <>
                <Image style={styles.biglock} source={require('../../assets/check.png')} />
              </>}
            </View>
          </View>
          <View style={styles.buttons}>
            <TouchableOpacity onPress={() => onSelectFish(fish, fish.id)} disabled={!fishes.includes(fish.id) || selected.id === fish.id}>
              <Image style={styles.select} source={!fishes.includes(fish.id) || selected.id === fish.id ? require('../../assets/select1.png') : require('../../assets/select.png')} />
            </TouchableOpacity>
            {!fishes.includes(fish.id) && <>
              <TouchableOpacity onPress={handleUnlock} disabled={bank < fish.price} style={styles.buttonUnlock}>
                  <Image style={styles.unlock} source={bank < fish.price ? require('../../assets/unlock1.png') : require('../../assets/unlock.png')} />
              </TouchableOpacity>
            </>}
          </View>
          <View style={styles.fishContainer}>
            {FISHES.map((fish, i) => (
              <TouchableOpacity onPress={() => setFish(fish)} key={fish.id}>
                <View
                  style={[
                    styles.fishItem,
                    { borderColor: fish.id === id ? '#8DF' : '#FFE' },
                    { backgroundColor: fish.id === id ? '#FFFE' : '#FFF9' },
                  ]}>
                  <Image style={styles.fish} source={fish.image} />
                  {!fishes.includes(fish.id) && <Image style={styles.lock} source={require('../../assets/lock.png')} />}
                  {fish.id === selected.id && <Image style={styles.lock} source={require('../../assets/check.png')} />}
                  <Text style={styles.text}y>{fish.name}</Text>
                </View>
              </TouchableOpacity>
            ))}
          </View>
        </View>
      </View>
      <Back onClickMenu={onClickMenu} />
    </>
  );
}

const styles = StyleSheet.create({
  background: {
    resizeMode: 'cover',
    height: '100%',
    width: '100%',
  },
  container: {
    flex: 1,
  },
  previewImage: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  name: {
    textAlign: 'center',
    fontSize: 32,
    color: '#FFF',
    fontWeight: 'bold',
    alignItems: 'center',
  },
  layout: {
    flex: 1,
    flexDirection: 'column',
  },
  buttons: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
  },
  fishContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
    paddingBottom: 20,
    flexWrap: 'wrap',
  },
  preview: {
    flex: 1,
  },
  fishItem: {
    width: 80,
    height: 80,
    margin: 10,
    marginTop: 10,
    borderRadius: 80,
    borderColor: '#FFFE',
    borderWidth: 2,
    backgroundColor: '#FFF9',
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'column',
  },
  fish: {
    width: 50,
    height: 50,
  },
  biglock: {
    position: 'absolute',
    width: 20,
    height: 20,
    top: 0,
  },
  lock: {
    position: 'absolute',
    width: '30%',
    height: '30%',
    top: '45%',
    left: '70%',
  },
  activeFish: {
    width: 120,
    height: 120,
  },
  text: {
    marginTop: -5,
  },
  unlock: {
    width: 100,
    height: 32,
    marginLeft: 10,
    marginRight: 10,
  },
  select: {
    width: 100,
    height: 32,
    marginLeft: 10,
    marginRight: 10,
  },
  info: {
    marginTop: 10,
    marginBottom: 10,
  },
  about: {
    color: '#FFF',
    fontSize: 18,
    fontWeight: 'bold',
  },
});
