import { memo } from 'react';
import { G } from "react-native-svg";
import Tile from "./Tile";

function Layers(props) {
  const { layers } = props;
  return layers.map((row, r) =>
    row.map((b, c) => (
      <G key={`${r}-${c}`} x={b.x} y={b.y}>
        <Tile id={b} y={r} x={c} />
      </G>
    ))
  );
}

export default memo(Layers);
