import { Image, View, StyleSheet } from "react-native";
import { Scores } from "../Scores";

export default function Start (props) {
  const { start, level, scores } = props;
  if(start) {
    return null;
  }
  return (
    <View style={styles.container} pointerEvents="none">
      <Image style={styles.image} source={require('../../assets/start.png')} />
      {level === 11 && <Scores scores={scores} />}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    backgroundColor: '#FFF4',
    position: 'absolute',
    left: 0,
    top: 0,
    width: '100%',
    height: '100%',
    alignItems: 'center',
    justifyContent: 'center',
  },
  image: {
    height: 100,
    width: 152,
  },
});
