
import { memo } from 'react';
import Svg, { Rect, Defs, Stop, LinearGradient } from 'react-native-svg';
import { Backgrounds, Blocks, Bubbles, Fish } from '../../Components';
import { LEVELS } from '../../Levels/Maps';
import GameBg from './GameBg';
import Score from './Score';

function Renderer (props) {
  const { level, fish, graphics, distance, score, ghost, gameOver, y, posY, life, infinite, paused, completed } = props;
  const { blocks, bubbles, backgrounds, backgroundColor, backgroundColor2 } = LEVELS[level-1];
  return (
    <Svg shapeRendering="crispEdges" height="100%" style={{ position: 'absolute', width: '100%', height: '100%' }} pointerEvents="none" viewBox="0 0 667 375" fill="#0000" overflow="hidden">
      <GameBg backgroundColor={backgroundColor} backgroundColor2={backgroundColor2} />
      {graphics > 1 && <Backgrounds backgrounds={backgrounds} distance={distance} />}
      <Blocks distance={distance} blocks={blocks} graphics={graphics} infinite={infinite} />
      <Backgrounds backgrounds={backgrounds} distance={distance} front graphics={graphics} />
      <Fish
        fish={fish}
        ghost={ghost}
        y={y}
        gameOver={gameOver}
        posY={posY}
        graphics={graphics}
        animate={!paused && !completed && !gameOver}
      />
      <Score score={score} life={life} fish={fish} bubbles={bubbles} level={level} />
      <Bubbles score={score} distance={distance} bubbles={bubbles} />
    </Svg>
  );
}

export default memo(Renderer);
