import { memo } from "react";
import { Image, G } from "react-native-svg";
import Bubble from './Bubble';

function BubbleList(props) {
  const { bubbles, score } = props;

  return bubbles.map((bubble, i) =>
    <Bubble popped={score.includes(i)} key={bubble.x} bubble={bubble} />
  );
}

export default memo(BubbleList);
