import { StyleSheet, Text, View, Image, TouchableOpacity, Dimensions } from "react-native";
import { useState, useEffect } from "react";
import { LEVELS } from '../../Levels';
import { TILE } from '../../Constants/tiles';

const window = Dimensions.get('window');
const UNIT = Math.min(window.height, window.width);

export default function Home(props) {
  const { onClickMenu, fish, level } = props;
  const levelObject = LEVELS[level - 1];
  return (
    <>
      <Image style={[StyleSheet.absoluteFill, styles.background]} source={require("../../assets/home.png")} />
      <View style={styles.container}>
        <Image style={styles.title} source={require("../../assets/title.png")} />
        <View style={styles.menu}>
          <TouchableOpacity style={styles.button} onPress={() => onClickMenu('inventory')}>
            <Image style={styles.icon} source={fish.image} />
            <Text style={styles.text}>FISH</Text>
          </TouchableOpacity>
          <TouchableOpacity style={styles.button} onPress={() => onClickMenu('levels')}>
            <Image style={styles.icon} source={levelObject.image} />
            <Text style={styles.text}>LEVEL</Text>
          </TouchableOpacity>
          <TouchableOpacity style={styles.button} onPress={() => onClickMenu('arena')}>
            <Image style={styles.icon} source={TILE[129]} />
            <Text style={styles.text}>ARENA</Text>
          </TouchableOpacity>
          <TouchableOpacity style={styles.button} onPress={() => onClickMenu('settings')}>
            <Image style={styles.icon} source={TILE[127]} />
            <Text style={styles.text}>SETTINGS</Text>
          </TouchableOpacity>
        </View>
      </View>
    </>
  );
}

const styles = StyleSheet.create({
  background: {
    resizeMode: 'cover',
    height: '100%',
    width: '100%',
  },
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'column',
  },
  title: {
    height: UNIT / 8 * 1,
    width: '80%',
    marginBottom: UNIT / 8,
  },
  menu: {
    alignItems: 'center',
    flexDirection: 'row',
    marginBottom: 50,
    justifyContent: 'center',
    flexWrap: 'wrap',
  },
  button: {
    borderRadius: UNIT,
    backgroundColor: '#FFFE',
    padding: UNIT / 8 / 2,
    margin: UNIT / 8 / 5,
    shadowColor: '#171717',
    shadowOffset: {width: -2, height: 4},
    shadowOpacity: 0.2,
    shadowRadius: 3,
    elevation: 10,
  },
  icon: {
    height: UNIT / 8 * 1.5,
    width: UNIT / 8 * 1.5,
  },
  text: {
    fontSize: UNIT / 8 / 3.5,
    position: 'absolute',
    fontWeight: 'bold',
    textAlign: 'center',
    backgroundColor: '#FFF',
    width: UNIT / 8 * 1.8,
    paddingTop: UNIT / 8 / 20,
    paddingBottom: UNIT / 8 / 20,
    bottom: - UNIT / 8 / 10,
    left: UNIT / 8 / 3,
    color: '#8DF',
    borderWidth: UNIT / 8 / 30,
    borderColor: '#EEE',
  },
});
