import { View, TouchableWithoutFeedback, StyleSheet } from "react-native";
import { useEffect, useReducer, useCallback } from "react";
import { GameLoop } from "react-native-game-engine";
import { LEVELS } from "../../Levels/Maps";
import { isCollided, reducer } from "./utility";
import {
  FISH_BLOCK_SIZE,
  force,
  gravity,
  HEIGHT,
  initialState,
} from "./constants";
import Renderer from "./Renderer";
import Controls from "./Controls";

export default function Game(props) {
  const { onClickMenu, setCompleted, scores, level, fish, graphics = 0 } = props;
  const [state, dispatch] = useReducer(reducer, initialState);
  const {
    paused,
    time,
    life,
    gameOver,
    score,
    completed,
    tapTime,
    pos,
    ghost,
  } = state;
  const LEVEL = LEVELS[level - 1];
  const distance = time * LEVEL.velocity;
  const {
    image,
    blocks,
    bubbles,
    backgroundColor,
    speed = 0.009,
    infinite,
  } = LEVEL;

  const ground = HEIGHT - 25;
  const timePassed = (time - tapTime) * speed;
  const y =
    force * Math.sin(90) * timePassed - 0.5 * gravity * timePassed * timePassed;
  const posY = Math.min(pos - y, ground);

  const setPaused = useCallback((p) => dispatch({ paused: p }), [dispatch]);
  const reset = () => {
    dispatch({ ghost: 3000, pos: 160, tapTime: time });
  };

  const update = () => {
    const lap = Math.floor(distance / 2600) * 2600;
    const block = blocks.find((b) => {
      return isCollided(posY, distance - (infinite ? lap : 0) , b);
    });
    const popped = bubbles
      .map((b, i) => ({ ...b, i }))
      .filter((b) => {
        return isCollided(posY, distance, b, true);
      });
    const bumped = block && !block.e;
    const fallen = posY >= HEIGHT - FISH_BLOCK_SIZE;
    if (popped.length > 0) {
      dispatch({ score: [...new Set([...score, ...popped.map((x) => x.i)])] });
    }
    if (block && block.e === 1) {
      dispatch({ completed: true, ghost: 0 });
    } else if ((!ghost && bumped) || fallen) {
      if (life) {
        dispatch({ life: Math.max(life - 1, 0) });
      }
      if (life > 1) {
        reset();
      }
    }
  };

  const handlePress = () => {
    if (paused) {
      dispatch({ paused: false });
      return;
    }
    if (!gameOver && !completed) {
      dispatch({ tapTime: time, pos: posY });
    }
  };

  const handleUpdate = ({ time: { delta } }) => {
    dispatch({ delta });
    if (ghost) {
      dispatch({ ghost: Math.max(ghost - delta, 0) });
    }
    update();
  };

  useEffect(() => {
    if (life <= 0) {
      dispatch({ gameOver: true, ghost: 0 });
    }
  }, [life]);

  useEffect(() => {
    if (completed || (gameOver && level === 11)) {
      const total = score.reduce((t, i) => {
        return (bubbles[i].s || 1) + t;
      }, 0);
      setCompleted(total, true);
    }
  }, [completed, gameOver, level]);

  return (
    <View style={[{ flex: 1, backgroundColor }]}>
      <GameLoop
        running={!paused && !gameOver && !completed}
        onUpdate={handleUpdate}
      />
      <Renderer
        level={level}
        fish={fish}
        graphics={graphics}
        score={score}
        distance={distance}
        ghost={ghost}
        gameOver={gameOver}
        y={y}
        posY={posY}
        life={life}
        infinite={infinite}
        paused={paused}
        completed={completed}
      />
      <TouchableWithoutFeedback onPressIn={handlePress}>
        <View style={StyleSheet.absoluteFill} />
      </TouchableWithoutFeedback>
      <Controls
        onClickMenu={onClickMenu}
        gameOver={gameOver}
        paused={paused}
        completed={completed}
        image={image}
        setPaused={setPaused}
        score={score}
        bubbles={bubbles}
        level={level}
        scores={scores}
      />
    </View>
  );
}
