import { G } from "react-native-svg";
import Layers from "./Layers";

export default function Backgrounds(props) {
  const { backgrounds = [], distance, front, graphics } = props;
  const lap = Math.floor(distance / 640) * 640;
  return (
    <G y={0}>
      {backgrounds.filter(x => (front ? !x.v : !!x.v) && (!x.g || (graphics >= x.g))).map((layer, l) => (
        <G key={`${l}`} opacity={layer.o} y={layer.y} x={0 - distance * (layer.v || 1) - 64 + (layer.repeat ? lap : 0)}>
          <Layers layers={layer.layers} />
        </G>
      ))}
    </G>
  );
}
