import { G, Image } from "react-native-svg";
import { useEffect, memo } from "react";
import Animated, {
  useAnimatedProps,
  useSharedValue,
  withSpring,
  withTiming,
} from "react-native-reanimated";
import bubbleImage from "../../assets/bubble.png";
import bubbleImage10 from "../../assets/bubble10.png";

const SIZE = 32;
const SIZE10 = 64;

const AnimatedImage = Animated.createAnimatedComponent(Image);

function Bubble(props) {
  const {
    bubble: { x, y, s },
    popped,
  } = props;
  const moveY = useSharedValue(0);
  const size = useSharedValue(s === 10 ? SIZE10 : SIZE);
  const animatedProps = useAnimatedProps(() => ({
    y: moveY.value,
    width: size.value,
    height: size.value,
  }));
  useEffect(() => {
    if (popped) {
      moveY.value = withSpring(-(y + 32));
      size.value = withTiming(0.1);
    }
  }, [popped]);

  return (
    <G x={x} y={y} height={SIZE} width={SIZE}>
      <AnimatedImage
        animatedProps={animatedProps}
        height={s >= 10 ? SIZE10 : SIZE}
        width={s >= 10 ? SIZE10 : SIZE}
        href={s >= 10 ? bubbleImage10 : bubbleImage}
      />
    </G>
  );
}

export default memo(Bubble);
