import { useRef, useEffect } from "react";
import { G, Image, Rect } from "react-native-svg";
import Animated, {
  useSharedValue,
  useAnimatedProps,
  cancelAnimation, 
  withTiming,
  withRepeat,
} from "react-native-reanimated";

const AnimatedG = Animated.createAnimatedComponent(G);

const FISH_SIZE = 50;
const FISH_LEFT = 200;

export default function Fish(props) {
  const {
    posX = FISH_LEFT,
    gameOver,
    fish,
    posY = 0,
    ghost,
    y = 0,
    graphics = 2,
    animate = true, 
  } = props;
  const angle = -y;
  const opacity = ghost ? Math.abs(Math.sin(ghost / 100)) : 1;
  const rotation = useSharedValue(-1);
  const animatedProps = useAnimatedProps(() => ({
    transform: `scale(${rotation.value} 0.8 ) skewX(${fish.wingS || 0})`,
  }));
  useEffect(() => {
    rotation.value = !animate ? (rotation.value * 1) : withRepeat(withTiming(-0.5, { duration: 500 }), -1, true);
  }, [animate]);
  return (
    <>
      <G y={posY} x={posX}>
        <G
          transform={
            graphics > 1 ? `rotate(${Math.min(35, angle / 2)})` : undefined
          }
        >
          <G x={-FISH_SIZE / 2} y={-FISH_SIZE / 2}>
            {ghost > 0 && (
              <Image width={FISH_SIZE} height={FISH_SIZE} href={fish.ghost} />
            )}
            <Image
              width={FISH_SIZE}
              height={FISH_SIZE}
              href={gameOver ? fish.ghost : fish.model || fish.image}
              opacity={opacity}
            />
          </G>
          {fish.wing && !gameOver && (
            <G x={fish.wingX} y={fish.wingY}>
              <AnimatedG animatedProps={animatedProps}>
                <Image opacity={opacity} x={0} width={12} height={12} href={fish.wing} />
              </AnimatedG>
            </G>
          )}
        </G>
      </G>
    </>
  );
}
