import { View, StyleSheet } from "react-native";
import Svg, { G, Image } from "react-native-svg";
import { memo } from "react";
import { TILE } from "../../Constants/tiles";

const NUM = [
  TILE[107],
  TILE[108],
  TILE[109],
  TILE[110],
  TILE[111],
  TILE[112],
  TILE[113],
  TILE[114],
  TILE[115],
  TILE[116],
  TILE[117],
];

function Hud(props) {
  const { bank } = props;
  return (
    <View style={styles.container} pointerEvents="none">
      <Svg width="100" height="32" viewBox="0 0 100 32">
        <G x="0" y="0" width="100" height="32">
          <Image
            width="32"
            height="32"
            href={require("../../assets/bubble.png")}
          />
          <G x="4">
            {String(bank)
              .split("")
              .map((v, i) => (
                <Image
                  key={i}
                  x={14 * i}
                  width="32"
                  height="32"
                  href={NUM[Number(v)]}
                />
              ))}
          </G>
        </G>
      </Svg>
    </View>
  );
}
export default memo(Hud);

const styles = StyleSheet.create({
  container: {
    position: "absolute",
    top: 0,
    left: 0,
    margin: 20,
  },
});
