import { Game } from "../Game";

export default function Arena(props) {
  const { onClickMenu, fish, graphics, setCompleted, scores } = props;
  const handleClickMenu = () => {
    onClickMenu("home");
  };
  return (
    <>
      <Game
        onClickMenu={handleClickMenu}
        level={11}
        fish={fish}
        graphics={graphics}
        setCompleted={setCompleted}
        scores={scores}
      />
    </>
  );
}
