import { memo, useMemo } from 'react';
import { Back, GameOver, Start, Pause, Completed } from '../../Components';

function Controls (props) {
  const { level, gameOver, scores, score, bubbles, paused, completed, image, onClickMenu, setPaused } = props;
  const total = useMemo(() => score.reduce((t, i) => {
    return (bubbles[i].s || 1) + t;
  }, 0), [completed, gameOver]);
  return (
    <>
      <GameOver scores={scores} gameOver={gameOver} level={level} score={total} />
      <Start start={!paused} level={level} scores={scores} />
      <Completed completed={completed} image={image} level={level} score={total} scores={scores} />
      {gameOver || paused || completed ? <Back onClickMenu={onClickMenu} to="levels" /> : null}
      {!paused && !gameOver && !completed && <Pause pause={paused} setPause={setPaused} />}
    </>
  );
};

export default memo(Controls);