import { memo } from 'react';
import { Image, G, Text } from "react-native-svg";
import bubble from '../../assets/bubble.png';

function Score (props) {
  const { score, bubbles, life, fish, level } = props;
  const total = score.reduce((t, i) => {
    return (bubbles[i].s || 1) + t;
  }, 0);
  return (
    <G fontFamily="Arial, san-serif">
      <Image y={10} x={70} width={32} height={32} href={life < 1 ? fish.icon2 : fish.icon} />
      <Image y={10} x={40} width={32} height={32} href={life < 2 ? fish.icon2 : fish.icon} />
      <Image y={10} x={10} width={32} height={32} href={life < 3 ? fish.icon2 : fish.icon} />
      <Image y="47" x="10" width="24" height="24" href={bubble} />
      <Text fill="#ffcc00" fontSize="24" stroke="#aaa" strokeWidth="4" fontWeight="bold" x="44" y="66">{total}</Text>
      <Text fill="#ffcc00" fontSize="24" stroke="#fff" strokeWidth="1" fontWeight="bold" x="44" y="66">{total}</Text> 
      <Text fill="#ffcc00" fontSize="24" stroke="#aaa" strokeWidth="4" fontWeight="bold" x="300" y="30">
        {level <= 10 ? `Lv ${level}` : 'Arena'}
      </Text>
      <Text fill="#ffcc00" fontSize="24" stroke="#fff" strokeWidth="1" fontWeight="bold" x="300" y="30">
        {level <= 10 ? `Lv ${level}` : 'Arena'}
      </Text>
    </G>
  );
};

export default memo(Score);
