import { Image, G } from "react-native-svg";
import { TILE } from "../../Constants/tiles";
import { Animated } from "react-native";

const AnimatedImage = Animated.createAnimatedComponent(Image);

const UNIT = 32;

export default function Tile(props) {
  const { animation, id, p, y = 0, a = 0, l, h } = props;

  if ([3].includes(p)) {
    const index1 = animation.interpolate({
      inputRange: [0, 25, 50, 75, 100],
      outputRange: [0, 1, 0, 1, 0],
      easing: v => Math.round(v),
    });
    const index2 = animation.interpolate({
      inputRange: [0, 25, 50, 75, 100],
      outputRange: [1, 0, 1, 0, 1],
      easing: v => Math.round(v),
    });
    const style1 = { opacity: index1 };
    const style2 = { opacity: index2 };
    return (
      <>
        <AnimatedImage href={TILE[l[0] - 1]} width={UNIT} height={h || UNIT} y={y} style={style1} />
        <AnimatedImage href={TILE[l[1] - 1]} width={UNIT} height={h || UNIT} y={y} style={style2} />
      </>
    );
  }

  return a && id > 19 ? (
    <G y={y + 16} x={16}>
      <AnimatedImage
        href={TILE[id - 1]}
        width={UNIT}
        height={UNIT}
        y={-16}
        x={-16}
        style={{
          transform: [
            {
              rotateZ:
                animation &&
                animation.interpolate({
                  inputRange: [0, 100],
                  outputRange: ["0deg", "360deg"],
                }),
            },
          ],
        }}
      />
    </G>
  ) : (
    <Image href={TILE[id - 1]} width={UNIT} height={UNIT} y={y} />
  );
}
