import { memo } from "react";
import { G } from "react-native-svg";
import Block from "./Block";

function BlocksList(props) {
  const { blocks, animation } = props;

  return blocks.map((block, i) => (
    <G key={`${i}-${block.x}`} x={block.x} y={block.p === 1 ? 375 - block.h : 0}>
      <Block block={block} animation={animation} />
    </G>
  ));
}

export default memo(BlocksList);
