import { StatusBar } from 'expo-status-bar';
import { StyleSheet, View, Platform } from 'react-native';
import { useState, useEffect, useCallback } from 'react';
import AppLoading from 'expo-app-loading';
import { Audio } from 'expo-av';
import { Arena, Home, Game, Levels, Settings, Inventory, FISHES } from './Screens';
import { getItem, setItem } from './store';
import { Hud } from './Components';
import { cacheImages } from './utils';

export default function App() {
  const [ screen, setScreen ] = useState('home');
  const [ level, setLevel ] = useState(1);
  const [ maxLevel, setMaxLevel ] = useState(1);
  const [ fish, setFish ] = useState(FISHES[0]);
  const [ fishes, setFishes ] = useState([0]);
  const [ scores, setScores ] = useState([0, 0, 0]);
  const [ graphics, setGraphics ] = useState(2);
  const [ sound, setSound ] = useState(0);
  const [ audio, setAudio ] = useState();
  const [ bank, setBank ] = useState(0);
  const [ loaded, setLoaded ] = useState(false);
  const [ error, setError ] = useState(false);

  const handleClickMenu = useCallback((menu, level) => {
    setScreen(menu);
    if (menu === 'game') {
      setLevel(level);
    }
  }, [level, screen]);

  function handleSelectFish (fish, i) {
    setFish(fish);
    setItem('fish', i);
  }

  function handleSetFihes(fishes) {
    setFishes(fishes);
    setItem('fishes', fishes);
  }

  async function adjustBank (newBank) {
    await setItem('bank', newBank);
    setBank(newBank);
  }

  async function handleCompleted(total, topScore) {
    if(level === maxLevel) {
      setMaxLevel(Math.min(10, maxLevel + 1));
      await setItem('level', Math.min(10, maxLevel + 1));
    }
    const newBank = bank + total;
    setBank(newBank);
    await setItem('bank', newBank);
    if(topScore) {
      const newScore = [...scores, total];
      const newTopScore = newScore.sort((a,b) => b-a).splice(0, 3);
      await setItem('scores', newTopScore);
      setScores(newTopScore);
    }
  }
  
  async function playSound() {
    const { sound: mp3 } = await Audio.Sound.createAsync(
       require('./assets/1862.mp3')
    );
    setAudio(mp3);
    await mp3.setIsLoopingAsync(true);
    await mp3.playAsync(); 
  }

  useEffect(() => {
    return audio ? () => audio.unloadAsync() : undefined;
  }, [audio]);

  async function load(){
    setSound(0);
    setMaxLevel(Math.min(10, await getItem('level', 1)));
    setGraphics(await getItem('graphics', 2));
    setBank(await getItem('bank', 0));
    setFishes(await getItem('fishes', [0]));
    setScores(await getItem('scores', [0, 0, 0]));
    const index = await getItem('fish', 0);
    setFish(FISHES[index || 0] || FISHES[0]);
    await cacheImages();
  }

  useEffect(() => {
    if (loaded) {
      setItem('sound', sound);
      if(sound) {
        playSound();
      } else if(audio) {
        audio.unloadAsync();
        setAudio(null);
      }
    }
  }, [sound, loaded]);
  useEffect(() => {
    if (loaded) {
      setItem('graphics', graphics);
    }
  }, [graphics, loaded]);

  if(!loaded) {
    return (
      <AppLoading
        startAsync={load}
        onFinish={() => setLoaded(true)}
        onError={() => setError(true)}
      />
    );
  }
  return (
    <View style={styles.container}>
      {screen === 'arena' && <Arena scores={scores} onClickMenu={handleClickMenu} fish={fish} graphics={graphics} setCompleted={handleCompleted} />}
      {screen === 'home' && <Home onClickMenu={handleClickMenu} level={maxLevel} fish={fish}/>}
      {screen === 'game' && <Game onClickMenu={handleClickMenu} level={level} fish={fish} graphics={graphics} setCompleted={handleCompleted}/>}
      {screen === 'levels' && <Levels onClickMenu={handleClickMenu} maxLevel={maxLevel} />}
      {screen === 'inventory' && <Inventory onClickMenu={handleClickMenu} onSelectFish={handleSelectFish} fish={fish} fishes={fishes} setFishes={handleSetFihes} bank={bank} adjustBank={adjustBank} />}
      {screen === 'settings' && <Settings onClickMenu={handleClickMenu} setGraphics={setGraphics} graphics={graphics} setSound={setSound} sound={sound} />}
      {!['settings', 'arena', 'game'].includes(screen) && <Hud bank={bank}/>}
      <StatusBar hidden />
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#aef',
    ...(Platform.OS === 'web' ? {
      '-webkit-touch-callout': 'none',
      '-webkit-user-select': 'none',
      '-khtml-user-select': 'none',
      '-moz-user-select': 'none',
      '-ms-user-select': 'none',
      'user-select': 'none',

    } : {}),
  },
});
