import { View, Text, Image, StyleSheet } from "react-native";
import { Back } from "../../Components";
import Switch from "./Switch";

const GRAPHICS = [
  require("../../assets/long.png"),
  require("../../assets/bg.png"),
  require("../../assets/home.png"),
];

export default function Settings(props) {
  const { onClickMenu, setGraphics, setSound, graphics, sound } = props;
  return (
    <>
      <Image
        style={[StyleSheet.absoluteFill, styles.background]}
        source={GRAPHICS[graphics]}
      />
      <View style={{ flexDirection: "row" }}>
        <View
          style={{
            flex: 1,
            flexDirection: "row",
            padding: 40,
          }}
        >
          <Text style={{ fontSize: 24, color: "#FFF" }}>Graphics</Text>
          <Switch value={graphics} onSelect={setGraphics} />
        </View>
        <View
          style={{
            flex: 1,
            flexDirection: "row",
            padding: 40,
          }}
        >
          <Text style={{ fontSize: 24, color: "#FFF" }}>Sound</Text>
          <Switch value={sound} onSelect={setSound} type="toggle" />
        </View>
      </View>
      <View style={{ alignItems: "center" }}>
        <View style={{ padding: 5 }}>
          <Text style={styles.text}>Developers:</Text>
        </View>
        <View style={{ padding: 5 }}>
          <Text style={styles.text}>Freima Joy P. Delos Angeles</Text>
        </View>
        <View style={{ padding: 5 }}>
          <Text style={styles.text}>Kean Arvy G. Torio</Text>
        </View>
        <View style={{ padding: 5 }}>
          <Text style={styles.text}>Ruben John A. Borromeo</Text>
        </View>
        <View style={{ padding: 5 }}>
          <Text style={styles.text}>Adviser: Mrs. Rizalyn A. Capanas</Text>
        </View>
      </View>
      <Back onClickMenu={onClickMenu} />
    </>
  );
}

const styles = StyleSheet.create({
  background: {
    resizeMode: "cover",
    height: "100%",
    width: "100%",
  },
  text: {
    color: "#FFF",
    fontSize: 24,
    textShadowColor: '#000',
    textShadowOffset: {width: 1, height: 1},
    textShadowRadius: 2,
  }
});
