import { View, Text, StyleSheet } from "react-native";

export default function Scores(props) {
  const { scores = [] } = props;
  return (
    <View style={styles.container} pointerEvents="none">
      <View style={styles.score}>
        <Text style={styles.h1}>Top Scores</Text>
        <Text style={styles.h2}>{scores[0] || 0}</Text>
        <Text style={styles.h3}>{scores[1] || 0}</Text>
        <Text style={styles.h4}>{scores[2] || 0}</Text>
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  score: {
    padding: 10,
    alignItems: 'center',
  },
  h1: {
    fontSize: 24,
    fontWeight: "bold",
    color: "#ffcc00",
    textShadowColor: '#ffff',
    textShadowOffset: {width: 1, height: 1},
    textShadowRadius: 10,
  },
  h2: {
    fontSize: 32,
    fontWeight: "bold",
    color: "#F22",
  },
  h3: {
    fontSize: 24,
    fontWeight: "bold",
    color: "#A22",
  },
  h4: {
    fontSize: 18,
    fontWeight: "bold",
    color: "#822",
  },
});
