
import { memo } from 'react';
import { Rect, Defs, Stop, LinearGradient } from 'react-native-svg';

function GameBg (props) {
  const { backgroundColor, backgroundColor2 } = props;
  return (
    <>
      <Defs>
        <LinearGradient id="grad1" x1="0%" y1="0%" x2="0%" y2="100%">
          <Stop offset="0%" stopColor={backgroundColor} />
          <Stop offset="100%" stopColor={backgroundColor2 || "#e0f6fd"} />
        </LinearGradient>
      </Defs>
      <Rect x="-64" width="896" height="375" fill="url(#grad1)" />
    </>
  );
}

export default memo(GameBg);