import { FISH_BLOCK_SIZE, FISH_LEFT, HEIGHT } from "./constants";

export function isCollided(y, distance, b, isBubble = false) {
  const r1x = FISH_LEFT - FISH_BLOCK_SIZE / 2;
  const r1y = y - FISH_BLOCK_SIZE / 2;
  const r1w = FISH_BLOCK_SIZE;
  const r1h = FISH_BLOCK_SIZE;
  const r2x = b.x - distance;
  const r2y = isBubble || b.y ? b.y : b.p === 0 ? 0 : HEIGHT - b.h;
  const r2w = isBubble ? (b.s >= 10 ? 64 : 32) : (b.w || 32);
  const r2h = isBubble ? (b.s >= 10 ? 64 : 32) : (b.h || 32);
  // https://developer.mozilla.org/en-US/docs/Games/Techniques/2D_collision_detection
  if (
    r1x + r1w >= r2x && // r1 right edge past r2 left
    r1x <= r2x + r2w && // r1 left edge past r2 right
    r1y + r1h >= r2y && // r1 top edge past r2 bottom
    r1y <= r2y + r2h
  ) {
    // r1 bottom edge past r2 top
    return true;
  }
}

export const reducer = (state, action) => {
  const { completed } = state;
  if (action.delta && !completed) {
    return { ...state, time: state.time + action.delta };
  } else if (action) {
    return { ...state, ...action };
  }
  return state;
};
