import { useState, useRef, useEffect, useLayoutEffect } from "react";
import { Animated, View, Image, ScrollView, TouchableOpacity, Dimensions } from "react-native";
import { Back } from "../../Components";
import { LEVELS } from "../../Levels";
import { LAYOUTS } from "./constants";
import { styles } from "./styles";
import Tile from "./Tile";

export default function Levels(props) {
  const { onClickMenu, maxLevel } = props;
  const ref1 = useRef();
  const ref2 = useRef();
  const [reverse, setReverse] = useState(false);
  const translation = useRef(new Animated.Value(0)).current;
  const xAnim = useRef(new Animated.Value(0)).current;
  useEffect(() => {
      Animated.timing(translation, {
        toValue: !reverse ? 50 : 0,
        useNativeDriver: true,
        duration: 5000,
      }).start(() => setReverse(!reverse));
  }, [reverse]);
  useLayoutEffect(() => {
    setTimeout(() => {
      if(ref1 && ref1.current && ref2 && ref2.current){
        ref1.current.scrollTo({ x: ref2.current.x - 280, animated: true });
      }
    }, 100);
  }, []);
  return (
    <>
      <ScrollView
        scrollEventThrottle={16}
        onScroll={Animated.event(
          [{ nativeEvent: { contentOffset: { x: xAnim } } }],
          {useNativeDriver: false}
        )}
        ref={ref1}
        bounces={false}
        alwaysBounceHorizontal={false}
        contentContainerStyle={styles.scroll}
        horizontal
        showsHorizontalScrollIndicator={false}
      >
        <View style={styles.wrapper}>
          {LAYOUTS.map((layout, l) => (
            <Animated.View
              key={l}
              style={[
                styles.layout,
                {
                  top: layout.y,
                  left: layout.x,
                  transform: [{ 
                    translateX: layout.a ? 
                      Animated.add(Animated.multiply(layout.v, xAnim), translation) : 
                      Animated.multiply(layout.v, xAnim)
                  }],
                  opacity: layout.o,
                },
              ]}
            >
              {layout.tiles.map((row, r) => (
                <View key={r} style={styles.row}>
                  {row.map((col, c) => (
                    <Tile key={c} id={col} />
                  ))}
                </View>
              ))}
            </Animated.View>
          ))}
        </View>
        <View style={styles.container}>
          {LEVELS.filter(x => !x.infinite).map((level, i) => (
            <TouchableOpacity
              key={i}
              accessible
              onLayout={i+1 === maxLevel ? event => ref2.current = event.nativeEvent.layout : undefined}
              disabled={i+1 > maxLevel}
              style={[ styles.level, i+1 > maxLevel ? styles.locked: undefined, i+1 === maxLevel ? styles.current: undefined]}
              onPress={() => onClickMenu("game", i + 1)}
            >
              <View style={styles.tile}>
                <Image style={styles.image} source={i+1 > maxLevel ? level.disabled : level.image} />
                {i+1 > maxLevel && <Image style={styles.lock} source={require('../../assets/lock.png')} />}
              </View>
            </TouchableOpacity>
          ))}
        </View>
      </ScrollView>
      <Back onClickMenu={onClickMenu} />
    </>
  );
}
