import { Image } from "react-native";
import { Asset } from "expo-asset";
import { TILE, ASSET } from './Constants';

export function cacheImages() {
  const images = [
    ...TILE,
    ...ASSET,
      require('./assets/bg.png'),
      require('./assets/on.png'),
      require('./assets/off.png'),
      require('./assets/low.png'),
      require('./assets/medium.png'),
      require('./assets/high.png'),
      require('./assets/bubble.png'),
      require('./assets/bubble10.png'),
      require('./assets/done.png'),
      require('./assets/start.png'),
      require('./assets/game-over.png'),
      require('./assets/home.png'),
      require('./assets/long.png'),
      require('./assets/lock.png'),
      require('./assets/title.png'),
      require('./assets/unlock.png'),
      require('./assets/unlock1.png'),
      require('./assets/select.png'),
      require('./assets/select1.png'),
      require('./assets/buttons/back.png'),
      require('./assets/buttons/play.png'),
      require('./assets/buttons/pause.png'),
  ];
  return images.map(image => {
    if (typeof image === 'string') {
      return Image.prefetch(image);
    } else {
      return Asset.fromModule(image).downloadAsync();
    }
  });
}
