import { Image } from "react-native-svg";
import { TILE } from "../../Constants/tiles";

const UNIT = 32;

export default function Tile(props) {
  const { id, x = 0, y = 0 } = props;

  return (
    <Image
      href={TILE[id - 1]}
      width={UNIT}
      height={UNIT}
      x={x * UNIT}
      y={y * UNIT}
    />
  );
}
